/**
 * 
 * @param { number } num
 * @param { number } fiexdNum 
 * @param { boolean } onlyFiexd 
 * @param { boolean } fiexdFlag 0
 */
export function numberSplit(
  num,
  fiexdNum = 2,
  onlyFiexd = false,
  fiexdFlag = true
) {
  if (/^-?\d+(\.\d+)?$/.test(num + "")) {
    num = +num;
    if (onlyFiexd) {
      num = num.toFixed(fiexdNum);
    }
    let flag = false;
    if (num < 0) {
      num = -num;
      flag = true;
    }
    const nums = (num + "").split(".");
    let str = nums[0]
      .split("")
      .reverse()
      .join("");
    for (let i = 3; i < str.length; i += 4) {
      str = str.substring(0, i) + "," + str.substring(i, str.length);
    }
    let fiexd = "";
    if (fiexdFlag) {
      if (fiexdNum > 0) {
        if (nums[1] && nums[1].length >= fiexdNum) {
          fiexd = "." + nums[1];
        } else {
          fiexd =
            "." +
            (nums[1] || "") +
            "000000000000000000000000000000000".substr(
              0,
              fiexdNum - (nums[1] || "").length
            );
        }
      }
    } else {
      fiexd = nums[1] ? "." + nums[1] : "";
    }
    return (
      (flag ? "-" : "") +
      str
        .split("")
        .reverse()
        .join("") +
      fiexd
    );
  } else {
    return num || "";
  }
}

export function addZero(n, digit = 2) {
  const str = "0000000000000000000000000000" + n;
  return str.substr(str.length - digit, digit);
}
export function dateFmt(date = new Date(), fmt = "yyyy-MM-dd") {
  if (!(date instanceof Date)) {
    if (typeof date === "number" || /^\d{1,}$/.test(date)) {
      date = new Date(date);
    } else {
      return date || "";
    }
  } else if (date.toString() === "Invalid Date") {
    return "";
  }
  const M = date.getMonth() + 1;
  const d = date.getDate();
  const h = date.getHours();
  const m = date.getMinutes();
  const s = date.getSeconds();
  const obj = {
    yyyy: date.getFullYear(),
    yy: date.getYear(),
    MM: addZero(M),
    M: M,
    dd: addZero(d),
    d: d,
    HH: addZero(h),
    H: h,
    hh: addZero(h % 12),
    h: h % 12,
    mm: addZero(m),
    m: m,
    ss: addZero(s),
    s: s,
    S: date.getTime() % 1000
  };
  for (let key in obj) {
    fmt = fmt.replace(key, obj[key]);
  }
  return fmt;
}
export const throttle = function(fn, context, timer = 100) {
  let flag = true;
  return function(...arg) {
    if (flag) {
      fn.apply(context, arg);
      flag = false;
    }
    setTimeout(() => {
      flag = true;
    }, timer);
  };
};
